  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
}
a::-moz-focus-inner {
  border: 0;
}
a:hover,
a:active,
a:focus {
  outline: 0 !important;
  outline-offset: 0;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
img {
  width: 100%;
}
textarea {
  resize: none;
}
section {
